@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
   font-family: 'Montserrat', sans-serif;
   background-color: #1a1a1c;
   color: #fff; 
 }
.App {
  text-align: center;
  width: 100%;
  /* display: flex;
  flex-direction: column;
  align-items: center; */
 
  color: white;
}

.coin-search{
  /* margin-bottom: 64px; */
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
}

.coin-text{
  /* margin-bottom: 32px; */
  text-align: center;
}

.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: 2px solid #fff;
  
  color: #e2e2e2
}

.coin-input::-webkit-input-placeholder {
  color: #fff;
  font-size: large;
  font-weight: lighter;
}

.coin-input:-ms-input-placeholder {
  color: #fff;
  font-size: large;
  font-weight: lighter;
}

.coin-input::placeholder {
  color: #fff;
  font-size: large;
  font-weight: lighter;
}

/* PAGINATE STYLING */
.paginationBttns {
  width: 80%;
  height: 40px;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  /* border: 2px solid red; */
}

.paginationBttns a{
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid #2b2eff;
  color: white;
  cursor: pointer;
  font-weight: bold;
  outline: none;
}

.paginationBttns a:hover {
  color: white;
  background-color: #2b2eff;
}

.paginationActive a{
  color: white;
  background-color: #2b2eff;
  border: 1px solid #2b2eff;
  outline: none;
}









